import React from 'react'
import '../style.css'


function Hints() {
    return (
        <div className="hint-block">
            <div class="banner1"></div>

            <div>
                <p>É muito ruim quando termina o dia e você lembra que tinha que ter feito algo que não fez. Para evitar isso, siga estas dicas rápidas:</p>

                <h4>Organize suas tarefas diárias</h4>
                <p>Aproveite esta ferramenta para colocar suas tarefas em uma lista. Quando começar o dia, escreva uma lista de atividades importantes e secundárias que você precisa fazer.
                <br />Isso ajuda a definir prioridades do dia e não se esquecer de nenhum item. Desta forma você não precisa se preocupar em lembrar o que tem que fazer. É só ler e colocar a mão na massa.</p>

                <h4>Realize uma tarefa por vez</h4>
                <p>Esta é uma dica de gestão de tempo. Estamos acostumados a fazer muitas coisas ao mesmo tempo e ter acesso a muita informação. Porém, fazer mais de uma atividade ao mesmo tempo reduz seu desempenho em todas elas. 
                <br />Foque em fazer uma atividade por vez e só avance para a próxima quando tiver terminado a atual. Tente realizar as mais importantes primeiro.</p>

                <h4>Faça pausas entre as tarefas</h4>
                <p>Evite fazer uma tarefa seguida da outra. Procure dar um pequeno intervalo para descansar após terminar uma tarefa. Este intervalo pode lhe dar uma energia extra para a próxima.
                <br />Mas descanse somente o suficiente para relaxar e fazer as próximas, sem dar chance para bater aquela preguiça.</p>

                <h4>Reduza as distrações</h4>
                <p>Prepare o ambiente para cada uma das atividades. Elimine as fontes de distração como, por exemplo, redes sociais, conversas, televisão, jogos, etc. até que cumpra aquela tarefa. Você poderá voltar a elas assim que tiver concluído aquela tarefa.</p>


                <p class="obs">Aproveite esta ferramenta também para outras finalidades: lista de compras, lista de amigos para ligar, anotar algo rápido, etc. 
                Seu uso é <strong>gratuito</strong> e o conteúdo fica disponível na sua tela até você atualizar a página.</p>
            </div>

            <div class="banner2"></div>
        </div>
    )
}

export default Hints;
